<template>
    <div class="game-screen light-theme" ref="game_screen">
        <div class="dungeon">
            <span title="change ui theme..." @click="change_theme" @mouseenter="change_theme_button('preview')" @mouseleave="change_theme_button('revert')" ref="theme_button" class="theme_button">‚òÄÔ∏è</span><a href="https://twitter.com/Tweet_Dungeon" target="_blank"><strong style="color: #333;">Tweet_Dungeon</strong>&nbsp;<em style="color: #666;">@tweet_dungeon</em></a>
            <pre ref="dungeon_output"></pre>
        </div>

        <div ref="text_output" class="text-output">
            You See Stone Tiles.<br />
            But You Can't See Far.<br />
            The Air Smells Of Rock.
        </div>

        <PollButton ref="first_button" :button_text=first_btn_txt @click.native="clicked_button('first')" />
        <PollButton ref="second_button" :button_text=second_btn_txt @click.native="clicked_button('second')" />
        <PollButton ref="third_button" :button_text=third_btn_txt @click.native="clicked_button('third')" />
        <PollButton ref="fourth_button" :button_text=fourth_btn_txt @click.native="clicked_button('fourth')" />
    </div>
</template>

<style lang="stylus" scoped>
    .light-theme
        --bg-color #fff
        --text-color #333
        --button-text-color #2388ee
        --button-background-color rgba(69, 148, 230, 0.25)
    .dim-theme
        --bg-color #15202B
        --text-color #fff
        --button-text-color #2388ee
        --button-background-color rgba(69, 148, 230, 0.25)
    .dark-theme
        --bg-color #000000
        --text-color #fff
        --button-text-color #2388ee
        --button-background-color rgba(69, 148, 230, 0.25)
    .game-screen
        width 100%
        height 100%
        background var(--bg-color)
        padding 0.75em
        box-shadow 0px 0px 13px 0px inset rgba(0, 0, 0, 0.25)
        display grid
        grid-template-columns 100%
        grid-template-rows 1fr 8fr repeat(4, 1fr)
        row-gap 0.5em
        .theme_button:hover
            cursor pointer
            text-shadow 2px 2px 2px rgba(0,0,0,0.25)
        a
            text-decoration none
            font-size 1em
            padding 0.5em
            border-radius 0.25em
            color var(--text-color)
        a:hover
            text-decoration underline
            font-size 1.025em
            // background linear-gradient(to right, #FAB13D, #DED09F)
            background linear-gradient(to right, #2388EE, #45AAFF)
            box-shadow 3px 3px 9px 0px rgba(0, 0, 0, 0.25)
        .dungeon
            padding -10px -10px 0px -10px
            margin-bottom -10px
            text-align center
            pre
                color var(--text-color)
        .text-output
            color var(--text-color)
            font-weight 300
            font-size 1.2em
            line-height 1.3em
            display grid
            align-content center
            margin-top 0
</style>

<script>
    let dungeon = [], dungeon_width = 25, dungeon_height = 15, min_room_size = 4, max_room_size = 5, room_buffer = 2

    let tileset = {
        out_of_bounds: '‚èπÔ∏è',
        0: '‚¨ú', // FLOOR
        1: '‚èπÔ∏è', // WALL
        2: 'üì∂', // STARTING STAIRS
        3: 'üï≥Ô∏è', // EXIT HOLE
        4: 'üíä', // MEDICINE
        5: 'üê∏', // GOBLIN
        6: 'üíÄ', // SKELETON
        7: 'üëª', // GHOST
        8: 'üêÆ', // MINOTAUR
        9: 'üíé'
    }

    let player = {
        sprite: { normal: 'üò∂', out_of_bounds: 'üòµ', worried: 'üòÆ', scared: 'üò¨', super_scared: 'üò®' },
        position: { x: 0, y: 0 }
    }

    let enemy_list = []
    let enemy_stats = [
        {name: 'goblin', health: 4, min_attack: 2, max_attack: 3, defense: 2},
        {name: 'skeleton', health: 2, min_attack: 0, max_attack: 3, defense: 1},
        {name: 'ghost', health: 3, min_attack: 1, max_attack: 2, defense: 3},
        {name: 'minotaur', health: 6, min_attack: 2, max_attack: 4, defense: 2}
    ]

    import PollButton from './PollButton.vue'

    export default {
        name: 'GameScreen',
        components: { PollButton },
        data() {
            return {
                current_theme: 'light',
                first_btn_txt: '‚¨ÜÔ∏è move north',
                second_btn_txt: '‚¨áÔ∏è move south',
                third_btn_txt: '‚û°Ô∏è move east',
                fourth_btn_txt: '‚¨ÖÔ∏è move west',
            }
        },
        methods: {
            change_theme() {
                if (this.current_theme == 'light') {
                    this.$refs.game_screen.classList.remove('light-theme')
                    this.$refs.theme_button.innerText = 'üåì'
                    this.$refs.game_screen.classList.add('dim-theme')
                    this.current_theme = 'dim'
                }
                else if (this.current_theme == 'dim') {
                    this.$refs.game_screen.classList.remove('dim-theme')
                    this.$refs.theme_button.innerText = 'üåë'
                    this.$refs.game_screen.classList.add('dark-theme')
                    this.current_theme = 'dark'
                }
                else if (this.current_theme == 'dark') {
                    this.$refs.game_screen.classList.remove('dark-theme')
                    this.$refs.theme_button.innerText = '‚òÄÔ∏è'
                    this.$refs.game_screen.classList.add('light-theme')
                    this.current_theme = 'light'
                }
            },

            change_theme_button(action) {
                if (this.current_theme == 'light') {
                    action == 'preview' ? this.$refs.theme_button.innerText = 'üåì' : this.$refs.theme_button.innerText = '‚òÄÔ∏è'
                }
                else if (this.current_theme == 'dim') {
                    action == 'preview' ? this.$refs.theme_button.innerText = 'üåë' : this.$refs.theme_button.innerText = 'üåì'
                }
                else if (this.current_theme == 'dark') {
                    action == 'preview' ? this.$refs.theme_button.innerText = '‚òÄÔ∏è' : this.$refs.theme_button.innerText = 'üåë'
                }
            },

            clicked_button(button) {
                let action = this.$refs[`${button}_button`].button_text.toLowerCase()

                if (action.indexOf('move north') >= 0) this.move_player('up')
                else if (action.indexOf('move west') >= 0) this.move_player('left')
                else if (action.indexOf('move east') >= 0) this.move_player('right')
                else if (action.indexOf('move south') >= 0) this.move_player('down')
                else if (action.indexOf('attack') >= 0) {
                    if (action.indexOf('north') >= 0) this.battle(enemy_list[`${player.position.x}${player.position.y-1}`])
                    else if (action.indexOf('east') >= 0) this.battle(enemy_list[`${player.position.x+1}${player.position.y}`])
                    else if (action.indexOf('south') >= 0) this.battle(enemy_list[`${player.position.x}${player.position.y+1}`])
                    else if (action.indexOf('west') >= 0) this.battle(enemy_list[`${player.position.x-1}${player.position.y}`])
                }
                // else if () 

                this.update_dungeon_display()
                this.$store.commit('update_time')
            },

            move_player(dir) {
                if (dir == 'up') {
                    let tile = dungeon[player.position.y-1][player.position.x]
                    if (tile == 0 || tile == 2) player.position.y--
                    else if (tile == 3) {
                        
                        
                    }
                    else if (tile == 4) {
                        player.position.y--
                        dungeon[player.position.y][player.position.x] = 0
                        this.$store.commit('heal_player')
                    }
                    else if (tile == 9) {
                        player.position.y--
                        dungeon[player.position.y][player.position.x] = 0
                        this.$store.commit('collect_loot')
                    }
                }
                else if (dir == 'right') {
                    let tile = dungeon[player.position.y][player.position.x+1]
                    if (tile == 0 || tile == 2) player.position.x++
                    else if (tile == 3) {
                        
                    }
                    else if (tile == 4) {
                        player.position.x++
                        dungeon[player.position.y][player.position.x] = 0
                        this.$store.commit('heal_player')
                    }
                    else if (tile == 9) {
                        player.position.x++
                        dungeon[player.position.y][player.position.x] = 0
                        this.$store.commit('collect_loot')
                    }
                }
                else if (dir == 'left') {
                    let tile = dungeon[player.position.y][player.position.x-1]
                    if (tile == 0 || tile == 2) player.position.x--
                    else if (tile == 3) {
                        
                    }
                    else if (tile == 4) {
                        player.position.x--
                        dungeon[player.position.y][player.position.x] = 0
                        this.$store.commit('heal_player')
                    }
                    else if (tile == 9) {
                        player.position.x--
                        dungeon[player.position.y][player.position.x] = 0
                        this.$store.commit('collect_loot')
                    }
                }
                else if (dir == 'down') {
                    let tile = dungeon[player.position.y+1][player.position.x]
                    if (tile == 0 || tile == 2) player.position.y++
                    else if (tile == 3) {
                        
                    }
                    else if (tile == 4) {
                        player.position.y++
                        dungeon[player.position.y][player.position.x] = 0
                        this.$store.commit('heal_player')
                    }
                    else if (tile == 9) {
                        player.position.y++
                        dungeon[player.position.y][player.position.x] = 0
                        this.$store.commit('collect_loot')
                    }
                }
            },

            battle(enemy) {
                console.log('ATTACKING!')
                enemy.type.health -= this.$store.state.player.attack
                if (enemy.type.health <= 0) dungeon[enemy.y][enemy.x] = 0
                this.$store.commit('collect_loot')
            },

            generate_new_dungeon() {
                dungeon = []
                enemy_list = []

                for (let row=0; row<dungeon_height; row++) {
                    dungeon.push([])
                    for (let col=0; col<dungeon_width; col++) {
                        dungeon[row].push(1)
                    }
                }

                let rooms = []

                for (let row=1; row<dungeon_height-1; row++) {
                    for (let col=1; col<dungeon_width-1; col++) {
                        let room_type = Math.floor(Math.random()*(max_room_size+1))

                        if (room_type >= min_room_size) {
                            if (row+(room_type-1) >= dungeon_height-1) continue;
                            else if (col+(room_type-1) >= dungeon_width-1) continue;
                            else {
                                let good_spot = true
                                for (let y=-room_buffer; y<=room_type; y++) {
                                    for (let x=-room_buffer; x<=room_type; x++) {
                                        try {
                                            if ((dungeon[row+y][col+x] == 0)) good_spot = false
                                        }
                                        catch {
                                            continue;
                                        }
                                    }
                                }
                                if (!good_spot) continue;
                                else {
                                    let special = null
                                    rooms.push({'x': col, 'y': row, 'size': room_type, 'special': special})
                                    for (let y=0; y<room_type; y++) {
                                        for (let x=0; x<room_type; x++) {
                                            dungeon[row+y][col+x] = 0
                                        }
                                    }
                                }
                            }
                        }
                    }
                }

                rooms.forEach((room, index) => {
                    if (index < rooms.length-1) {
                        let next_room = rooms[index+1], connected = false

                        let x = 0, y = 0
                        let random_y = Math.floor(Math.random()*2)

                        while (!connected) {

                            dungeon[(room.y+random_y)+y][room.x+x] = 0
                            dungeon[(room.y+random_y)+(y+1)][room.x+(x+1)] = 0

                            if (room.x+x < next_room.x) x++
                            else if (room.x+x > next_room.x) x--
                            else if (room.y+y < next_room.y) y++
                            else if (room.y+y > next_room.y) y--
                            else {
                                connected = true
                            }
                        }
                    }
                })

                rooms.forEach((room, index) => {
                    if (index == 0) {
                        room.special = 'entry'
                        dungeon[room.y+(Math.floor(room.size/2))][room.x+(Math.floor(room.size/2))] = 2
                    }
                    else if (index == rooms.length-1) {
                        room.special = 'exit'
                        dungeon[room.y+(Math.floor(room.size/2))][room.x+(Math.floor(room.size/2))] = 3
                    }
                    else {
                        let rnd_num = Math.floor(Math.random()*7) + 1
                        if (rnd_num <= 4) {
                            let enemy = Math.floor(Math.random()*3) + 6, x_pos = room.x+(Math.floor(room.size/2)), y_pos = room.y+(Math.floor(room.size/2))
                            dungeon[y_pos][x_pos] = enemy
                            enemy_list[`${x_pos}${y_pos}`] = {type: enemy_stats[enemy-5], x: x_pos, y: y_pos}
                        }
                        else if (rnd_num == 5) {
                            dungeon[room.y+(Math.floor(room.size/2))][room.x+(Math.floor(room.size/2))] = 4
                        }
                        else if (rnd_num >= 6) {
                            dungeon[room.y+(Math.floor(room.size/2))][room.x+(Math.floor(room.size/2))] = 9
                        }
                    }
                })

                player.position.x = Math.floor(rooms[0].x + (rooms[0].size/2))
                player.position.y = Math.floor(rooms[0].y + (rooms[0].size/2))

                // SPAWN ENEMY NEXT TO PLAYER, FOR TESTING-PURPOSES
                let rnd_enemy = Math.floor(Math.random()*3)+6
                dungeon[player.position.y][player.position.x+1] = rnd_enemy
                enemy_list[`${player.position.x+1}${player.position.y}`] = { type: enemy_stats[rnd_enemy-5], x: player.position.x+1, y: player.position.y }

                this.update_dungeon_display()
            },

            update_dungeon_display() {
                this.first_btn_txt = '‚¨ÜÔ∏è move north'
                this.second_btn_txt = '‚¨áÔ∏è move south'
                this.third_btn_txt = '‚û°Ô∏è move east'
                this.fourth_btn_txt = '‚¨ÖÔ∏è move west'

                let camera_display = [[], [], [], [], [], [], []]

                camera_display.forEach((row, index) => {
                    let y = -3 + index
                    for (let x=-3; x<=3; x++) {
                        let tile_y = player.position.y+y, tile_x = player.position.x+x
                        
                        if (tile_y < 0) row.push(tileset.out_of_bounds)
                        else if (tile_y >= dungeon.length) row.push(tileset.out_of_bounds)
                        else if (tile_x < 0) row.push(tileset.out_of_bounds)
                        else if (tile_x >= dungeon[tile_y].length) row.push(tileset.out_of_bounds)
                        else row.push(tileset[dungeon[tile_y][tile_x]])
                    }
                })

                if (player.position.y < 0 || player.position.y >= dungeon.length) camera_display[3][3] = player.sprite.out_of_bounds
                else if (player.position.x < 0 || player.position.x >= dungeon[player.position.y].length) camera_display[3][3] = player.sprite.out_of_bounds
                // else if ()
                else camera_display[3][3] = player.sprite.normal

                this.$refs.dungeon_output.innerText = `${camera_display[0].join('')}\n${camera_display[1].join('')}\n${camera_display[2].join('')}\n${camera_display[3].join('')}\n${camera_display[4].join('')}\n${camera_display[5].join('')}\n${camera_display[6].join('')}`
                twemoji.parse(this.$refs.dungeon_output)

                let north_tile = dungeon[player.position.y-1][player.position.x]
                let south_tile = dungeon[player.position.y+1][player.position.x]
                let east_tile = dungeon[player.position.y][player.position.x+1]
                let west_tile = dungeon[player.position.y][player.position.x-1]

                if (north_tile >= 5 && north_tile <= 8) this.first_btn_txt = `üëä attack ${enemy_list[`${player.position.x}${player.position.y-1}`].type.name} north`
                else if (north_tile == 3) this.first_btn_txt = 'üï≥Ô∏è exit to next dungeon'
                else if (north_tile == 4) this.first_btn_txt = 'üíä grab health north'
                else if (north_tile == 9) this.first_btn_txt = 'üíé grab loot north'
                
                if (south_tile >= 5 && south_tile <= 8) this.second_btn_txt = `üëä attack ${enemy_list[`${player.position.x}${player.position.y+1}`].type.name} south`
                else if (south_tile == 3) this.second_btn_txt = 'üï≥Ô∏è exit to next dungeon'
                else if (south_tile == 4) this.second_btn_txt = 'üíä grab health south'
                else if (south_tile == 9) this.second_btn_txt = 'üíé grab loot south'
                
                if (east_tile >= 5 && east_tile <= 8) this.third_btn_txt = `üëä attack ${enemy_list[`${player.position.x+1}${player.position.y}`].type.name} east`
                else if (east_tile == 3) this.third_btn_txt = 'üï≥Ô∏è exit to next dungeon'
                else if (east_tile == 4) this.third_btn_txt = 'üíä grab health east'
                else if (east_tile == 9) this.third_btn_txt = 'üíé grab loot east'
                
                if (west_tile >= 5 && west_tile <= 8) this.fourth_btn_txt = `üëä attack ${enemy_list[`${player.position.x-1}${player.position.y}`].type.name} west`
                else if (west_tile == 3) this.fourth_btn_txt = 'üï≥Ô∏è exit to next dungeon'
                else if (west_tile == 4) this.fourth_btn_txt = 'üíä grab health west'
                else if (west_tile == 9) this.fourth_btn_txt = 'üíé grab loot west'
            },
        },
        mounted() {
            this.generate_new_dungeon()
        }
    }
</script>