import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
	state: {
		time: { minutes: 0, hours: 0, days: 0},
		player: { name: 'squeaks', level: 1, health: 13, attack: 2, loot: 0 }
	},
	mutations: {
		update_time(state) {
			state.time.minutes += 20
			if (state.time.minutes >= 60) {
				state.time.minutes = 0
				state.time.hours++
				if (state.time.hours >= 24) {
					state.time.hours = 0
					state.time.days++
				}
			}
		},

		heal_player(state) {
			state.player.health += 8
			if (state.player.health > 13) state.player.health = 13
		},

		collect_loot(state) {
			state.player.loot += Math.floor(Math.random()*10)+3
		}
	},
	actions: {},
	modules: {}
})
